/* tools中使用的css */

/*接口结果提示 start*/
.no-data, .no-data1, .try-refresh, .try-refresh1 {
  width: 100%;
  font-size: 15px;
  color: red;
  text-align: center;
}

.no-data1, .try-refresh1 {
  padding-top: 100px;
}

/*接口结果提示 end*/

/*加载提示 start*/
.loading-tips, .dy-loading-tips {
  padding: 50px 0;
  text-align: center;
}

.dy-loading-tips {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.loading-tips p, .dy-loading-tips p {
  font: 12px/36px "microsoft yahei";
    color:#777;
}

.loading-tips img, .dy-loading-tips img {
  width: 20px !important;
  height: 20px !important;
}

/*加载提示 end*/

/*翻页start*/
/*自定义新样式请复制翻页所有样式，然后修改.page名称，再分别修改样式内容，例子见下面翻页第二版*/
.page {
  padding: 30px 0 0;
  font-size: 0;
  text-align: center;
}

.page > a {
  display: inline-block;
  vertical-align: middle;
  width: 28px;
  height: 28px;
  margin: 0 5px;
  font: normal 14px/28px "Arial";
  color: #999;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  cursor: pointer;
}

.page > .first-page, .page > .last-page {
  width: 48px;
  font-family: "Microsoft YaHei";
}

.page > .page-down, .page > .page-up {
  width: 58px;
  font-family: "Microsoft YaHei";
}

/*有效按钮（可点击）,自定义样式时请注意valid和invalid*/
.page > .curr, .page > .valid:hover {
  font-weight: bold;
  color: #fff;
  background-color: #d9341d;
  border-color: #d9341d;
}

/*无效按钮（不能点击）*/
.page > .invalid {
  cursor: auto;
}

/*翻页end*/

/*翻页第二版 start*/
.page-small {
  width: 100%;
  height: 26px;
  padding: 40px 0;
  font-size: 0;
  text-align: center;
}

.page-small a {
  display: inline-block;
  vertical-align: middle;
  height: 24px;
  line-height: 24px;
  margin-right: 10px;
  font-size: 14px;
  color: #555;
  background-color: #fff;
  text-align: center;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  width: 28px;
  font-family: "Arial";
  cursor: pointer;
}

.page-small a.first-page, .page-small a.last-page {
  width: 48px;
  font-family: "Microsoft YaHei";
}

.page-small a.page-up, .page-small a.page-down {
  width: 62px;
  font-family: "Microsoft YaHei";
}

/*有效按钮（可点击）*/
.page-small .valid:hover {
  background-color: #ff8e20;
  border: 1px solid #ff8e20;
  color: #fff;
}

.page-small .curr {
  font-weight: bold;
  font-size: 16px;
  background-color: #ff8e20;
  color: #fff;
}

/*无效按钮（不能点击）*/
.page-small > .invalid {
  cursor: auto;
}

/*翻页第二版 end*/

/*视频弹窗*/
.share-video {
  display: block;
  width: 32px;
  height: 32px;
  background: url(../image/shareVedio.png) no-repeat;
}

.share-video:hover {
  background-position: 0 -32px;
}

.video-box {
  width: 1010px;
  height: 707px;
  overflow: hidden;
  position: fixed;
  z-index: 10002;
  left: 50%;
  margin-left: -505px;
  top: 50%;
  margin-top: -353px;
  filter: alpha(opacity=0);
  opacity: 0;
  -webkit-opacity: 0;
  -moz-opacity: 0;
}

.video-cont {
  position: relative;
  width: 980px;
  height: 595px;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.6);
  filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr=#99ffffff, endColorstr=#99ffffff);
  -webkit-border-radius: 5px;
  border-radius: 5px;
}

.video-tit {
  position: relative;
  width: 100%;
  color: #000;
  text-align: center;
  filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr=#99ffffff, endColorstr=#99ffffff);
  -webkit-border-radius: 5px;
  border-radius: 5px;
}

.video-tit p {
  position: absolute;
  right: 20px;
  top: 9px;
}

.vpro {
  width: 100%;
  height: 5px;
  position: absolute;
  margin-top: -2.5px;
  top: 50%;
  background: #000;
  left: -100%;
}

/*图库弹出层样式*/
.opa80 {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10000;
  background: url(../image/opa80.png) repeat;
}

.pop-remove {
  z-index: 10001;
}

.pop-close {
  display: block;
  position: fixed;
  right: 0;
  top: 0;
  width: 72px;
  height: 72px;
  background: url(../image/tc_close.png) no-repeat;
  z-index: 10001;
}

.pop-close:hover {
  background: url(../image/tc_closeH.png) no-repeat center;
}

.pop-btn {
  display: block;
  position: fixed;
  width: 70px;
  height: 150px;
  top: 50%;
  margin-top: -75px;
  z-index: 10002;
}

.pop-prev {
  left: 0;
  background: url(../image/tc_left.png) no-repeat center;
}

.pop-prev:hover {
  background: url(../image/tc_leftH.png) no-repeat center;
}

.pop-next {
  right: 0;
  background: url(../image/tc_right.png) no-repeat center;
}

.pop-next:hover {
  background: url(../image/tc_rightH.png) no-repeat center;
}

.pop-box {
  display: inline-block;
  position: fixed;
  z-index: 10002;
  left: 0;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.6);
  filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr=#99ffffff, endColorstr=#99ffffff);
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.pop-tit {
  position: absolute;
  width: 100%;
  text-align: center;
  height: 32px;
  bottom: 35px;
  left: 0;
}

.pop-tit > span {
  display: inline-block;
  vertical-align: middle;
  *zoom: 1;
  *display: inline;
  *vertical-align: middle;
  *margin-top: auto;
  height: 32px;
  padding: 0 20px;
  font: normal 14px/32px "microsoft yahei";
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
  filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr=#99000000, endColorstr=#99000000);
  -webkit-border-radius: 32px;
  border-radius: 32px;
}

.webDa:hover {
  cursor: pointer
}

.high-light{
    color:#ab3103;
    font-weight:normal;
}

.no-more {
    text-align: center;
    line-height: 80px;
    color: #666;
}
