@charset "utf-8";
/* CSS Document */
body,html{font-family: "Microsoft YaHei" !important;}
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, dd, dl, dt, li, ol, ul,input,select,button,textarea { padding:0; margin:0; border:none;}
body{position: relative;}
input,button,select,textarea,a,img{outline:none; }/*去掉超链接或按钮点击时出现的虚线框黄色边框*/
::-moz-focus-inner{border:0px;}/*火狐的私有属性去掉点击时边框*/
html{ width:100%;height:100%; overflow-x:hidden;}
a img {	border:none;}
ul, ul li, ol li, li {	list-style:none;}
table {	border-collapse: collapse;border-spacing:0;}
input, textarea{border: 0;}
a { text-decoration: none; color: #333; }
a:hover { text-decoration: none; }
.clearfix:after{visibility: hidden;display: block;font-size: 0;content: ".";clear: both;height: 0;}
* html .clearfix {zoom:1;}
*:first-child + html .clearfix {zoom:1;}
img{vertical-align:top; border: none;}
.fl {float:left;}
.fr {float:right;}
.none{display:none;}
.nrp{text-overflow: ellipsis; white-space: nowrap; word-break:keep-all; overflow: hidden;}/*设置文字超出部分为省略号*/
li{ text-decoration:none;}
li:hover{text-decoration: none;}
i{font-style:normal;}
.hide{overflow: hidden;  text-overflow: ellipsis;  white-space: nowrap;}
.wrap { margin: 0 auto; width: 1200px; }

.flexslider{ position: relative; width: 100%; height: 100%; margin: 0; overflow: hidden; }

/** 透明遮罩:数字代表不同透明度 **/
.mask60 { position: absolute; z-index: 10; display: block; width: 100%; background: url(../image/opc60.png) repeat; }
.mask50 { position: absolute; z-index: 10; display: block; width: 100%; background: url(../image/opc50.png) repeat; }
.mask30 { position: absolute; z-index: 10; display: block; width: 100%; background: url(../image/opc30.png) repeat; }

/** 动画 **/
.scale,.change {
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}
.scale:hover {
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transform: scale(1.1);
}

