/************ 导航分类 ***********/
.tag{ width: 1180px; margin: 0px 20px 0 0; padding: 10px 0px 10px 20px; background-color: #fff;}
.tag > li{ position: relative; line-height: 50px; font-size: 0; border-bottom: 1px solid #e5e5e5;}
.tag > li > hr{ position: absolute; left:0; top: 50px; border: 0; width: 100%; height: 1px; padding: 0; margin: 0; color:#e6e6e6; background-color:#e6e6e6; overflow: hidden; }
.tag > li > p{ display:inline-block; vertical-align: top; max-width: 1010px; ;font-size: 0; }
.tag > li > span{ display:inline-block; vertical-align: middle; line-height:50px; font-size: 14px; color: #333;}
.tag > li a,.tag > li label{ display:inline-block; *zoom:1; *display:inline; vertical-align:top; *vertical-align:middle; height: 26px; line-height: 26px; margin: 12px 12px 12px 0; padding: 0 10px; font: 14px "微软雅黑"; color: #333; }
.tag > li a.curr,.tag > li a:hover{ color: #fff;background-color: #409cff; -webkit-border-radius:2px; border-radius:2px; }
.tag > li.bot_none{border-bottom: none;}

