/*列表样式*/
.modle_pic{ position: relative; display: block; width: 100%; height: 288px; overflow: hidden;  text-align: center; }
.modle_title1{ height: 36px; line-height: 36px; margin-top: 7px; font-size: 16px; font-weight: bold; overflow: hidden;text-overflow: ellipsis; white-space: nowrap; }
.modle_title1 > a{ color:#fff; }
.modle_title1 i{ display:inline-block; vertical-align: top; margin: 10px 8px 0 0 ; width: 4px; height:16px;  background-color: #fd950f; -webkit-border-radius: 4px; border-radius: 4px; }
/*.summary{ height: 66px; font-size: 14px;line-height: 22px;color: #ddeeff;text-align: justify; overflow: hidden; }*/
/*.summary a{ padding-left: 6px; color: #ff9635; }*/
.pic_more{ position: absolute; left: 50%; top: 50%; display: none; vertical-align: middle; width: 120px; height: 40px; line-height: 40px; margin: -20px 0 0 -60px; font-size: 16px; color: #fff; text-align: center;
    background-color: rgba(0, 0, 0, 0.6); filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0,startColorstr= #99000000,endColorstr= #99000000); -webkit-border-radius: 3px; -moz-border-radius: 3px; border-radius: 3px;
    -webkit-transition-duration: 500ms; -moz-transition-duration: 500ms; transition-duration: 500ms; }
.pic_more:hover { background-color: rgba(0, 0, 0, 0.8); filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0,startColorstr= #C8000000,endColorstr= #C8000000); }

/*图片效果*/
.scale_pic{ vertical-align: top; width: 100%; height: 100%; -webkit-transform: scale(1.2); -moz-transform: scale(1.2); transform: scale(1.2); -webkit-transition-duration: 500ms; -moz-transition-duration: 500ms; transition-duration: 500ms; }
.scale_pic_small{ -webkit-transform: scale(1); -moz-transform: scale(1); transform: scale(1);  }
.animate_pic li:hover .scale_pic,.animate_pic dl:hover .scale_pic,.animate_pic a:hover .scale_pic {-webkit-transform: scale(1.3); -moz-transform: scale(1.3); transform: scale(1.3); }
.animate_pic li:hover .shadow_pic{ display: block; }

/**************公用图标样式***************/
.data_info { height: 26px; line-height: 26px; margin-top: 10px; font-size: 14px; color: #333; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
.icon { display: inline-block; vertical-align: top; *zoom: 1; *display: inline; *vertical-align: middle; *margin-top: auto; width: 26px; height: 26px; margin-right: 8px; background: url(../image/icon.png)  no-repeat 0 0; }
.icon_tel { background-position: 0 -32px; }
.icon_addr { background-position: 0 -58px; }
.icon_tit { width: 7px; height: 7px; vertical-align: middle; background-position: 0 -84px; }
/***********分页************/
.page{ padding: 40px 0 60px; font-size: 0; text-align: center;}
.page > a{ display: inline-block; width: 51px; height: 38px; margin:0 5px; font: normal 14px/38px "Arial"; color: #555; text-align: center; background-color: #fff; -webkit-border-radius: 4px; border-radius:4px; cursor: pointer;}
.page > .first_page,.page > .last_page{ width: 58px; font-family: "Microsoft YaHei";}
.page > .page_up,.page > .page_down{ width: 77px; font-family: "Microsoft YaHei";}
.page > .curr,.page > a:hover{color: #fff; background-color: #b52815; border-color: #fff;cursor: pointer;}

/**********下拉框样式*****************/
.daq-btn-default .daq-caret {position: absolute; top: 50%; right: 13px; margin-top: -2px; }
.daq-caret { display: inline-block; width: 0; height: 0; margin-left: 2px; vertical-align: middle; border-top: 4px solid #9d9d9d; border-right: 4px solid transparent; border-left: 4px solid transparent;}
.daq-select { position:relative; z-index:2; float: left; height:42px; }
.daq-filter { float:left; width:125px; height: 40px; text-indent: 10px; font-size: 14px; text-align: left; line-height: 40px;}
.daq-dropdown-menu {position: absolute; top: 39px; left: 0; z-index: 1000; display: none; float: left; width: 178px; margin: 2px 0 0; font-size: 14px; list-style: none; background-color: #fff; border: 1px solid #ccc; }
.daq-inputadd-none {border-top: 1px solid #e0e0e0; border-bottom: none!important;}
.daq-dropdown-search {width:178px;}
.daq-show {display: block;}
.daq-hidden {display: none;}

.order_select .daq-select{ height:28px; }
.white_se .daq-filter{ width: 100%; height: 100%; line-height: 26px; }
.white_se .daq-btn-default{ width: 76px; height: 24px; background-color: #fff;}
.white_se .daq-dropdown-menu{ width: 150px; }
.daq-btn-default{color: #222;border: 1px solid #dadada;}
.daq-select-line{background-color: #ccc;}
.daq-btn-default.active {color: #222; border-color: #dadada;}
.daq-dropdown-menu{border:1px solid #dadada;}
.daq-drop-ul{background-color: #fff;}
.daq-dropdown-menu .daq-drop-ul>li>a{color: #222;}
.daq-dropdown-menu .daq-drop-ul>li:hover {background: #ddf2ff;}
.daq-dropdown-menu .daq-drop-ul>li>.halfsel {background: #ddf2ff;}
.daq-dropdown-menu .daq-drop-ul>li>.active>a {color: #222;}
.daq-dropdown-menu .daq-drop-ul>li>ul>li>.active {background: #ddf2ff;}
.daq-dropdown-menu .daq-drop-ul>li>ul>li>.active>a {color: #0087bd;}






